.no-webp .inicio{
    background-image: url(../../../build/img/hero2.jpg);
}

.webp .inicio{
    background-image: url(../../../build/img/hero2.jpg);
}

.header{
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;

        @include tablet{
            min-height: 70rem;
        }

        .contenido-header{
            @include tablet{
                min-height: 70rem;
            }
        }
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1{
        color: $blanco;
        font-size: 3.5rem;
        font-weight: $bold;
        line-height: 2;
        max-width: 60rem;
    }
}